@font-face {
    font-family: 'NotoSansCJKkr-Medium';
	src : url(../fonts/NotoSansCJKkr-Medium.otf) format('truetype');
}

html, body {
    height: 100%;
    font-family : NotoSansCJKkr-Medium;
    background-color : #EFEEEE;
}

#header{
    height: 70px;
    position: fixed;
    top :0;
    width : 100%;
    z-index : 10;
    background-color: #dad8d8;
}


#blogTitle{
    position: absolute;
    top:1.5%;
    left:5%;
}

#blogTitle a{
    color: black; 
    text-decoration: none;
}

#navMenu{
    position: absolute;
    top:1.5%;
    right:5%;
    list-style-type: none;
}

#navMenu li{
    display: inline-block;
    padding:0px 30px;
}

#navMenu li a{
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    border-radius: 50px;
    background: #dad8d8;
    box-shadow:  5px 5px 10px #969595, 
             -5px -5px 10px #ffffff;
    color: black;
}

#navMenu li a:hover{
    border-radius: 50px;
    background: linear-gradient(145deg, #c4c2c2, #e9e7e7);
    box-shadow:  5px 5px 10px #969595, 
             -5px -5px 10px #ffffff;
}

.clicked{
    border-radius: 50px !important;
    background: #dad8d8 !important;
    box-shadow: inset 5px 5px 10px #969595, 
            inset -5px -5px 10px #ffffff !important;
}

.content {
    color : gray;
}

#mainSection{
    display: flex;
    height: 85%;
    background-color: #EFEEEE;
}

#joelPicture{
    width:400px;
    height:400px;
    margin:auto;
}

#sideMenuBar {
    position : fixed;
    top: 13%;
    left: 3%;
    height : 50%;
    width : 260px;
    border-radius: 50px;
    background: #EFEEEE;
    box-shadow:  25px 25px 50px #dcdbdb, 
                -25px -25px 50px #ffffff;
}

#sideMenuBar ul {
    padding : 5px;
}

.parentMenu{
    max-width:100%;
    list-style-type: none;
    color : black;
    padding : 10px;
    margin : 10px 20px;
    font-size: 20px;
    font-weight: bold;
    
}

.childMenu{
    padding-left : 100px !important;
    text-align: center;
}

.childMenu li{
    font-size : 18px;
    margin : 15px 0px;
    padding : 5px 0px;
    list-style-type: none;
    border-radius: 34px;
    background: #EFEEEE;
    box-shadow:  6px 6px 11px #a5a4a4, 
             -6px -6px 11px #ffffff;
    color : black;
    cursor: pointer;
}

.childMenu li:hover{
    border-radius: 34px;
    background: linear-gradient(145deg, #d7d6d6, #ffffff);
    box-shadow:  6px 6px 11px #a5a4a4, 
             -6px -6px 11px #ffffff;
}

#postList{
    position : absolute;
    top: 100px;
    left: 25%;
    width : 70%;
}

#postStyle{
    padding : 5% 8% 10% 8%;
    margin : 0 auto;
    margin-bottom: 10%;
    line-height: 180%;
    max-width: 680px;
    border-radius: 14px;
    background: #EFEEEE;
    box-shadow:  5px 5px 10px #acabab, 
                -5px -5px 10px #ffffff;
}

.postContent {
    list-style-type: none;
    padding : 30px;
    min-width : 70%;
    margin : 50px;
    cursor: pointer;
    border-radius: 14px;
    background: #EFEEEE;
    box-shadow:  5px 5px 10px #acabab, 
             -5px -5px 10px #ffffff;
}

.postContent:hover {
    border-radius: 14px;
    background: #EFEEEE;
    box-shadow: inset 5px 5px 10px #cbcaca, 
            inset -5px -5px 10px #ffffff;
}

.postContent a{
    text-decoration: none;
    color : black;
}

blockquote {
    display:block;
    background: #fff;
    margin: 0px 50px 0px 50px;
    padding: 30px 30px 30px 50px;
    position: relative;
    font-family: Georgia, serif;
    font-size: 18px;
    line-height: 1.2;
    color: #666;
    text-align: justify;
    border-left: 15px solid #c76c0c;
    border-right: 5px solid #c76c0c;
    -moz-box-shadow: 2px 2px 15px #ccc;
    -webkit-box-shadow: 2px 2px 15px #ccc;
    box-shadow: 2px 2px 15px #ccc;
}

.highlight { 
    background : black; 
    color : white; 
    padding: 10px; 
}